require('codemirror/lib/codemirror.css');
require('codemirror/mode/javascript/javascript.js');

import CodeMirror from 'codemirror';
import * as babylon from 'babylon';
import traverse from 'babel-traverse';
import template from 'babel-template';
import generate from 'babel-generator';
import * as t from 'babel-types';

/** START: code in eval */
let __history = [];
let __marks = {};
function l(loc, ...values) {
  if (__marks[loc.line + ',' + loc.column]) {
    __marks[loc.line + ',' + loc.column].push({ loc, values: values.map(v => JSON.stringify(v)) });
  } else {
    __marks[loc.line + ',' + loc.column] = [{ loc, values: values.map(v => JSON.stringify(v)) }];
  }
  __history.push({
    ...loc,
    values: __marks[loc.line + ',' + loc.column].slice(0)
  });
  checkInfiniteLoop(__marks[loc.line + ',' + loc.column]);
  checkInfiniteLoop(__history);
}
function ll(loc, ...values) {
  __marks[loc.line + ',' + loc.column] = [{ loc, values: values.map(v => JSON.stringify(v)) }];
  __history.push({
    ...loc,
    values: __marks[loc.line + ',' + loc.column].slice(0)
  });
  checkInfiniteLoop(__history);
}
function _line(loc) {
  __history.push(loc);
  checkInfiniteLoop(__history);
}

function checkInfiniteLoop(arr) {
  if (arr.length > 10000) {
    throw new Error('there is probably an infinite loop');
  }
}
/** END */

const injectBefore = `__marks = {}; __history = [];\n`;
const injectAfter = `\n[__marks, __history]`;

const code = localStorage.getItem('js-playground-code-stash') || '';
const codeLines = code.split('\n').length;

const editor = CodeMirror(document.getElementById('code'), {
  value: code,
  mode: 'javascript',
  lineNumbers: true
});
const headerHeight = 30;
editor.setSize(600, window.innerHeight - headerHeight);

editor.on('change', () => {
  editor.operation(function() {
    localStorage.setItem('js-playground-code-stash', editor.getValue());
    evaluate(editor.getValue());
  });
});

editor.on('scroll', instance => {
  document.querySelector('.result').scrollTo(0, editor.getScrollInfo().top);
});

setTimeout(() => {
  evaluate(code);
}, 100);

function evaluate(code) {
  code = code
    .replace(/([\s|\n])\/\/\/([^\/][^\n]+)/g, '$1l($2)')
    .replace(/([\s|\n])\/\/\/\/([^\/][^\n]+)/g, '$1ll($2)');
  let ast;
  try {
    ast = babylon.parse(code);
  } catch (e) {
    displayErr(e);
    throw e;
  }
  const buildRequire = template(`
    _line(LOC);
  `);

  const visitor = {
    CallExpression(path) {
      if (path.node.callee.name === 'l' || path.node.callee.name === 'll') {
        path.node.arguments.unshift(
          t.objectExpression([
            t.objectProperty(t.identifier('line'), t.numericLiteral(path.node.loc.start.line)),
            t.objectProperty(t.identifier('column'), t.numericLiteral(path.node.loc.start.column))
          ])
        );
      }
    },
    ExpressionStatement(path) {
      if (
        path.node.expression.callee &&
        (path.node.expression.callee.name === '_line' ||
          path.node.expression.callee.name === 'l' ||
          path.node.expression.callee.name === 'll')
      ) {
        return;
      }
      insertLineTrack(path);
    },
    VariableDeclaration(path) {
      if (path.parent.type === 'ForStatement') {
        return;
      }
      insertLineTrack(path);
    },
    ReturnStatement(path) {
      if (path.parent.type === 'ForStatement') {
        return;
      }
      insertLineTrack(path);
    },
    ForStatement(path) {
      path.get('body').unshiftContainer(
        'body',
        buildRequire({
          LOC: t.objectExpression([
            t.objectProperty(t.identifier('line'), t.numericLiteral(path.node.loc.start.line)),
            t.objectProperty(t.identifier('column'), t.numericLiteral(path.node.loc.start.column))
          ])
        })
      );
    }
  };

  function insertLineTrack(path) {
    path.insertBefore(
      buildRequire({
        LOC: t.objectExpression([
          t.objectProperty(t.identifier('line'), t.numericLiteral(path.node.loc.start.line)),
          t.objectProperty(t.identifier('column'), t.numericLiteral(path.node.loc.start.column))
        ])
      })
    );
  }

  traverse(ast, visitor);
  const codeToEval = injectBefore + generate(ast).code + injectAfter;
  console.log(codeToEval);

  try {
    const [marks] = eval(codeToEval);
    displayResult(marks, codeLines);
  } catch (e) {
    displayErr(e);
    throw e;
  }
}

function displayResult(marks, codeLines) {
  const lineHeight = document.querySelector('.CodeMirror-line').getBoundingClientRect().height;
  const linesHTML = Object.keys(marks)
    .map(
      key => `<div class="result-item" style="top: ${(marks[key][0].loc.line - 1) * lineHeight +
        1}px"><span class="result-step-indicator"></span>${marks[key]
        .map(r => `<pre class="result-span">${r.values}</pre>`)
        .join('')}</div>
`
    )
    .join('');
  document.querySelector('.result').innerHTML = `<div class="result-inner">${linesHTML}</div>`;
  document.querySelector('.result-inner').style.height = codeLines * lineHeight + 8 + 'px';

  //Set line css class to v line
  document
    .querySelectorAll('.value-log-line')
    .forEach(line => line.classList.remove('value-log-line'));
  Object.keys(marks).forEach(key => {
    editor.removeLineClass(marks[key][0].loc.line - 1, 'wrap', 'value-log-line');
    editor.addLineClass(marks[key][0].loc.line - 1, 'wrap', 'value-log-line');
  });

  // scroll result into editor
  document.querySelector('.result').scrollTo(0, editor.getScrollInfo().top);
}

function displayErr(err) {
  document.querySelector('.result').innerHTML = `<div class="error">${err}</div>`;
}

/**
 *
 *
 * play/pause
 *
 *
 */
let playIntervalHandle = null;
let playIndex = 0;
function play() {
  clearInterval(playIntervalHandle);
  playIntervalHandle = setInterval(() => {
    goToProgress(playIndex);
    playIndex++;
    if (playIndex === __history.length) {
      clearInterval(playIntervalHandle);
    }
  }, 300);
}
function pause() {
  clearInterval(playIntervalHandle);
}
function replay() {
  playIndex = 0;
  clearInterval(playIntervalHandle);
  play();
}
window.pause = pause;
window.play = play;
window.replay = replay;

/**
 *
 *
 * mouse move to step back/forward
 *
 *
 */

function handleStepMove(e) {
  if (playIntervalHandle !== null) {
    clearInterval(playIntervalHandle);
    playIndex = 0;
    playIntervalHandle = null;
  }
  const padding = 5; // padding on left and right edge for matching start and end.
  let historyIndex = 0;
  if (e.offsetX <= padding) {
    historyIndex = 0;
  } else if (e.offsetX > e.target.offsetWidth - padding) {
    historyIndex = __history.length - 1;
  } else {
    historyIndex = Math.round(
      ((e.offsetX - padding) / (e.target.offsetWidth - 2 * padding)) * __history.length
    );
  }

  goToProgress(historyIndex);
}

function handleStepExit() {
  document.querySelector('.inner-bar').style.width = 0 + 'px';
  displayResult(__marks, codeLines);
  __history.forEach(h => {
    editor.removeLineClass(h.line - 1, 'background');
  });
  __lastIndex = null;
}
window.handleStepMove = handleStepMove;
window.handleStepExit = handleStepExit;

/**
 *
 *
 * go to certain step in history
 *
 *
 */

let __lastIndex = null;
let __blinkTimer = null;
let __lastLines = [];
function goToProgress(stepIndex) {
  const stepWidth = document.querySelector('.step-bar').offsetWidth / __history.length;
  document.querySelector('.inner-bar').style.width = (stepIndex + 1) * stepWidth + 'px';

  if (!__history[stepIndex]) return;
  if (__lastIndex !== null && __lastIndex === stepIndex) return;
  __lastIndex = stepIndex;

  clearTimeout(__blinkTimer);
  __lastLines.forEach(line => {
    editor.removeLineClass(line, 'background');
  });

  __lastLines = [];
  for (let i = 4; i >= 0; i--) {
    if (!__history[stepIndex - i]) continue;

    editor.addLineClass(__history[stepIndex - i].line - 1, 'background', `track-line step-${i}`);
    __lastLines.push(__history[stepIndex - i].line - 1);

    if (i === 0) {
      let line0El = document.querySelector('.step-0');
      line0El.classList.add('blink');
      __blinkTimer = setTimeout(() => {
        line0El.classList.remove('blink');
      }, 200);
    }
  }

  const marks = {};
  for (let i = 0; i <= stepIndex; i++) {
    const step = __history[i];
    if (step.values) {
      marks[`${step.line},${step.column}`] = step.values;
    }
  }
  displayResult(marks, codeLines);
}
